import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  CardText,
  Button,
  CardFooter,
  CardImg,
  CardSubtitle,
  Input,
} from "reactstrap";
import "./index.scss";
import { connect } from "react-redux";
import { documentActions } from "../../store/file/file.action";
import ModalPopUp from "../modalPopup/modal-popup";
import Loader from "../loader/loader";

class Upload extends Component {
  state = {
    bgImg: require("../../assets/bglow.png"),
    uploadFileApk: null,
    uploadFileIPA: null,
  };

  componentWillUnmount() {
    this.props.reset();
  }

  uploadAndroid = (event) => {
    const file = event.target.files[0];
    if (!file.name.match(/\.(apk)$/)) return;
    const data = new FormData();

    data.append("apps", file);
    this.props.uploadAndroid({ data });
    event.target.value = null;
  };

  uploadIOS = (event) => {
    const file = event.target.files[0];
    if (!file.name.match(/\.(ipa)$/)) return;
    const data = new FormData();
    data.append("apps", file);
    this.props.uploadIOS({ data });
    event.target.value = null;
  };

  render() {
    const {
      uploadFileAndroidError,
      uploadFileAndroidSuccess,
      uploadFileAndroidLoading,

      uploadFileIOSError,
      uploadFileIOSSuccess,
      uploadFileIOSLoading,

      reset,
    } = this.props;

    const loading = uploadFileAndroidLoading || uploadFileIOSLoading;
    const error = uploadFileAndroidError || uploadFileIOSError;
    const success = uploadFileAndroidSuccess || uploadFileIOSSuccess;
    const isOpen = error || success;
    const popupMsg = success
      ? "APP UPLOADED SUCCESSFULLY"
      : error
      ? "Error Occured"
      : null;
    const color = success ? "success" : error ? "danger" : "primary";
    return (
      <>
        <div className="bg">
          <img src={this.state.bgImg} />
        </div>
        {loading && <Loader />}
        <ModalPopUp
          modalOpen={isOpen}
          popupMsg={popupMsg}
          className={color}
          heading={"Download Status..."}
          toggle={reset}
        />
        <Container className="download-container d-flex align-items-center justify-content-center">
          <Row>
            <Col>
              <div className="app-logo d-inline-flex align-items-center">
                <img
                  width={70}
                  height={70}
                  src={require("../../assets/logo.png")}
                />
                <span
                  style={{
                    color: "#fff",
                    fontSize: "1.5rem",
                  }}
                >
                  Red Smart
                </span>
              </div>
              <div style={{ color: "#fff", marginBottom: "10px" }}>
                <CardTitle>
                  Get your{" "}
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    FLIGHT
                  </span>{" "}
                  App
                </CardTitle>
                <CardSubtitle style={{ fontSize: "12px" }}>
                  This app for managing airasia flights
                </CardSubtitle>
              </div>
              <Row>
                <Col>
                  <div className="btn btn-danger d-inline-flex align-items-center justify-content-center">
                    <Input
                      type="file"
                      accept=".apk"
                      id="apk"
                      className=" d-none"
                      onChange={this.uploadAndroid}
                    />
                    <label
                      htmlFor="apk"
                      className="d-inline-flex align-items-center justify-content-center"
                    >
                      <img
                        width={20}
                        height={20}
                        src={require("../../assets/apple.svg")}
                      />
                      <span
                        style={{
                          fontSize: "13px",
                          paddingLeft: "5px",
                          whiteSpace: "nowrap",
                        }}
                      >
                        Upload For Android{" "}
                      </span>
                    </label>
                  </div>
                </Col>
                <Col>
                  <div className="btn btn-danger d-inline-flex align-items-center justify-content-center">
                    <Input
                      type="file"
                      accept=".ipa"
                      id="ipa"
                      className=" d-none"
                      onChange={this.uploadIOS}
                    />
                    <label
                      htmlFor="ipa"
                      className="d-inline-flex align-items-center justify-content-center"
                    >
                      <img
                        width={20}
                        height={20}
                        src={require("../../assets/apple.svg")}
                      />
                      <span
                        style={{
                          fontSize: "13px",
                          paddingLeft: "5px",
                          whiteSpace: "nowrap",
                        }}
                      >
                        {" "}
                        Upload For IOS{" "}
                      </span>
                    </label>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return state.file.fileReducer;
};

const actionCreators = {
  downloadAndroid: documentActions.downloadAndroid,
  downloadIOS: documentActions.downloadIOS,
  uploadAndroid: documentActions.uploadAndroid,
  uploadIOS: documentActions.uploadIOS,
  reset: documentActions.reset,
};
export default connect(mapStateToProps, actionCreators)(Upload);
